import './index.scss';

import classNames from 'classnames';
import { Typography } from 'components/ui';
import { useEffect, useState } from 'react';

// ----------------------------------------------------------------------

export type NavItemProps = {
  label: string;
  itemId: string;
  iconName?: string;
  subNav?: NavItemProps[];
};

export type SideNavProps = {
  isExpanded?: boolean;
  items: NavItemProps[];
  activeItemId: string;
  onSelect?: ({ itemId }: { itemId: string }) => void;
};

const SideNav = ({ isExpanded, activeItemId, onSelect, items }: SideNavProps) => {
  const [activeSubNav, setActiveSubNav] = useState({
    expanded: true,
    selectedId: activeItemId
  });

  useEffect(() => {
    setActiveSubNav((originalSubNav) => ({
      expanded: originalSubNav.expanded,
      selectedId: activeItemId
    }));
  }, [activeItemId]);

  const handleClick = (itemId: string): void => {
    // call the callback if supplied
    onSelect?.({ itemId });
  };

  const handleSubNavExpand = (item: NavItemProps): void => {
    if (activeSubNav.expanded) {
      const currentItemOrSubNavItemIsOpen: boolean =
        // either the parent item is expanded already
        item.itemId === activeSubNav.selectedId ||
        // or one of its expandable children is selected
        (item.subNav &&
          item.subNav.some((_subNavItem) => _subNavItem.itemId === activeSubNav.selectedId)) ||
        false;

      setActiveSubNav({
        expanded: item.subNav && item.subNav.length > 0 ? !currentItemOrSubNavItemIsOpen : false, // disable expansion value, if not expandable
        selectedId: item.itemId
      });
    } else {
      setActiveSubNav({
        expanded: !!(item.subNav && item.subNav.length > 0), // expand if expandable
        selectedId: item.itemId
      });
    }
  };

  return (
    <>
      {items.length > 0 && (
        <nav role="navigation" aria-label="side-navigation" className="side-nav">
          <ul className="side-nav__list">
            {items.map((item: NavItemProps) => {
              const ElemBefore = item.iconName;
              const isItemSelected: boolean = item.itemId === activeSubNav.selectedId;
              const isActiveTab: boolean =
                // item is expanded and
                activeSubNav.expanded &&
                // either the current expandable section is selected
                (isItemSelected ||
                  // or some item in the expandable section of the current item is selected or active
                  (item.subNav &&
                    item.subNav.some(
                      (_subNavItem: NavItemProps) => _subNavItem.itemId === activeSubNav.selectedId
                    )) ||
                  false);

              return (
                <>
                  <li
                    key={item.itemId}
                    className={classNames('side-nav__item', {
                      'side-nav__item--active': isItemSelected
                    })}
                  >
                    <button
                      type="button"
                      onClick={(): void => {
                        handleSubNavExpand(item);
                        handleClick(item.itemId);
                      }}
                      className={classNames('side-nav__item-touch-area', {
                        'side-nav__item-touch-area--expanded': isExpanded
                      })}
                    >
                      {/** Prefix Icon Component */}
                      {ElemBefore && (
                        <img
                          src={`/assets/icons/ico-${item.iconName}.svg`}
                          alt={`${item.iconName} icon`}
                          className="side-nav__item-icon"
                        />
                      )}

                      {isExpanded && (
                        <Typography variant="head" className="side-nav__item-label">
                          {item.label}
                        </Typography>
                      )}
                    </button>
                  </li>

                  {item.subNav && item.subNav.length > 0 && isActiveTab && (
                    <ul className="side-nav__inner-list">
                      {item.subNav.map((subNavItem: NavItemProps) => {
                        const SubItemElemBefore = subNavItem.iconName;

                        return (
                          <li key={subNavItem.itemId} className="side-nav__inner-item">
                            <button
                              type="button"
                              onClick={(): void => {
                                setActiveSubNav({
                                  ...activeSubNav,
                                  selectedId: subNavItem.itemId
                                });
                                handleClick(subNavItem.itemId);
                              }}
                              className={classNames('side-nav__inner-item-touch-area', {
                                'side-nav__item--active':
                                  activeSubNav.selectedId === subNavItem.itemId
                              })}
                            >
                              {/** Prefix Icon Component */}
                              {SubItemElemBefore && (
                                <img
                                  src={`/assets/icons/ico-${item.iconName}.svg`}
                                  alt={`${item.iconName} icon`}
                                  className="side-nav__inner-item-icon"
                                />
                              )}

                              <Typography
                                variant="head"
                                className={classNames('side-nav__inner-item-label', {
                                  'side-nav__inner-item-label--active':
                                    activeSubNav.selectedId === subNavItem.itemId
                                })}
                              >
                                {subNavItem.label}
                              </Typography>
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  )}
                </>
              );
            })}
          </ul>
        </nav>
      )}
    </>
  );
};

export default SideNav;
